import Link from 'next/link'
import React from 'react'
import { Container, Row, Col, Button, Jumbotron, ListGroup, ListGroupItem,NavbarBrand } from 'reactstrap'
import styled from 'styled-components';
import Page from '../components/tuDien/page'
import Home from '../components/homepage/Home'
import * as GA from "../utils/analystics";
import NdhpLayout from '../components/NdhpLayout'
// import NdhpLayout from '../components/MyLayout'

const JumbotronI = styled(Jumbotron)`
  padding: 1 rem !important;
`;

const DivContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  background-color: DodgerBlue;
`;
const But = styled.a`
  background-color: #f1f1f1;
  margin: 5px;
  padding: 3px 3px;
  text-align: center;
  line-height: 40px;
  font-size: 25px;
`;

const I = styled.i`
    font-weight: 600;
    width: 30px; 
    height: 30px;
`;

export default class extends Page {
  render() {
    GA.trackPageView("index");
    return (
      <NdhpLayout {...this.props} navmenu={false} container={false}>
        <Container>
          <Home className="text-muted small" />
        </Container>
      </NdhpLayout>
    )
  }
}